.video-thumbnail {
    cursor: pointer;
    display: flex;
    flex-direction: column;

}

.video-thumbnail__item {
    width: 100%;
    position: relative;
}

.video-thumbnail__item > img {
    width: 100%;
}

.video-thumbail__item__cover {
    background-color: rgba(0, 0, 0, 0.5);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

.video-thumbnail__item:hover .video-thumbail__item__cover__wrapper {
    display: block;
}

.video-thumbail__item__cover__wrapper {
    display: none;
}

.video-thumbnail__title {
    margin-top: 0.5em;
    font-size: 12px;
}
