html {
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}

body {
    background-color: rgb(238, 238, 238);
    display: flex;
    justify-content: center;
}

#root {
    margin-top: 0;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}
