.home {
    margin: 5em 2em 2em;
}

@media (max-width: 500px) {
    .home {
        margin: 0 2em 2em;
    }
}

.home__info-box {
    display: flex;
    justify-content: center;
    margin-top: 5em;
}

.home__info-box__icon {
    display: inline-block;
    transform: translateY(0.4em);
}

.home__info-box__content {
    max-width: 40em;
}