

.control-icons {
    display: flex;
    justify-content: center;
}

.control-icons > div {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 10px;
}

.copy-link-icon {
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;

}

.settings-icon {
    cursor: pointer;
}

.copy-link-icon__prompt {
    color: red;
    position: absolute;
    top: -10px;
    display: none;
    
}
