.help {
    margin-top: 3em;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.help__content {
    max-width: 1000px;
    padding: 0.5em 2em;
}

.help__content__img {
    max-width: 100%;
}
.help__content__img--mobile {
    max-width: 250px;
    height: auto;
    object-fit: contain;
}


.help__content__img--mobile > img {
    width: 100%;
}

.help__content__mobile-images {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.help__content__mobile-images > *+* {
    margin-left: 1em;
}

@media (max-width: 500px) {
    .help__content__mobile-images > *+* {
        margin: 0
    }
}