.search {
    display: flex;
    flex-wrap: wrap;
    margin: 5em 2em 2em;
}

@media (max-width: 500px) {
    .search {
        margin: 0 2em 2em;
    }
}

.search__video-thumbnail {
    padding-top: 1em;
    max-width: 300px;
    margin-left: 1em;
}

.search__video-thumbnail:hover {
    transform: scale(1.1);
}
