.video-list {
    display: flex;
    flex-wrap: wrap;
}

.video-list__content {
    display: flex;
    flex-wrap: wrap;
}


.video-list__title {
    width: 100%;
    padding: 1em 0.5em 0.1em 0.5em;
    border-bottom: 1px solid #c1c1c1;
    display: flex;
    justify-content: space-between;
}

.video-list .video-list__video-thumbnail {
    margin-left: 1em;
}

.video-list__video-thumbnail {
    width: 20%;
    min-width: 200px;
    padding: 1em 0;
    transition: 250ms;
}

.video-list__video-thumbnail:hover {
    transform: scale(1.1);
}


@media (max-width: 495px) {
    .video-list__content {
     flex-direction: column;
     align-items: center;
    }
 
    .video-list .video-list__video-thumbnail {
        width: 90%;
        margin: 0;
    }
 }