.header {
    width: 100%;
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 0;
    z-index: 100;
}

.header__upper-bar {
    width: 100%;
    height: 2.5em;
    background-color: #252525;
    display: flex;
}

.header__logo {
    display: flex;
    align-items: center;
    height: 100%;
    padding: 0 1.5em;
    color: #b1afaf;
    cursor: pointer;
    text-decoration: none;
}

.header__logo > *+* {
    margin-left: 0.5em;
}

.header__help {
    display: flex;
    align-items: center;
    color: #b1afaf;
    cursor: pointer;
    margin-left: auto;
    margin-right: 1.5em;
    text-decoration: none;
}

.header__help > *+* {
    margin-left: 0.5em;
}


.header__search-desktop {
    width: 50%;
    margin-left: auto;
}

@media (max-width: 500px) {
    .header__search-desktop {
        display: none;
    }
}

.header__search-mobile {
    height: 2.5em;
    margin: 0 2em;
    display: none;
}

.header__search-mobile input {
    font-size: 16px;
}

@media (max-width: 500px) {
    .header__search-mobile {
        display: block;
    }

    .header {
        position: static;
    }
}