.time-input-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 10px;
}

.time-input-box__content {
    height: 3.5em;
    display: flex;
}

.time-input-box__content > input {
    height: 100%;
    width: 5em;
    text-align: center;
    border-radius: 0;
}

.time-input-box > label {
    margin-bottom: 0.2em;
}

.time-input-box__plus {
    border-radius: 0 1em 1em 0;
}

.time-input-box__minus {
    border-radius: 1em 0 0 1em;
}

.time-input-box__plus, .time-input-box__minus {
    height: 100%;
    width: 3.5em;
    background-color: #30c4f1;
    background-image: linear-gradient(to top, #30c4f1, #047597);
    color: white;
    border: 1px solid #188eb2;
    display: flex;
    align-items: center;
    justify-content: center;
}
