.save-video__icon {
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 10px;
}


.save-video-modal-content {
    font-size: initial;
    display: flex;
    flex-direction: column;
    padding: 1em 1.5em;
}

.save-video-modal-content > div {
    text-align: right;
    margin-top: 0.5em;
}

.save-video-modal-content > div > button {
    padding: 0.5em 1em;
    background-image: linear-gradient(to top, #32affb, #0077bf);
    color: white;
    border: 1px solid #0c82ff;
    cursor: pointer;
}

.save-video-modal-content > div > button:hover  {
    background-image: linear-gradient(to top, #67c3fb, #0094ee);
}

.save-video-modal-content__title {
    display: flex;
    flex-direction: column;
    font-size: 14px;
    font-weight: 600;
    color: #595959;
    font-family: sans-serif;
}

.save-video-modal-content__title > *+* {
    margin-top: 0.1em;
}