.info-box {
    display: flex;
    border: 1px solid #0969ce;
    background-color: #008abf;
    color: white;
    /* padding: 1.5em 2em 1.5em 0; */
}



.info-box__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.5em 2em;
    background-color: #077ba8;
}

.info-box__content {
    padding: 1em 2em;
}

@media (max-width: 500px) {
    .info-box {
        flex-direction: column;
    }

    .info-box__content {
        padding: 1em;
    }
}