#root {
    width: 100%;
}

.yt-loop {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 5em;
}

.yt-loop:focus {
    outline: none;
}

@media (max-width: 500px) {
    .yt-loop {
        margin: 0;
    }
}

.yt-loop__container {
    width: 80%;
    display: flex;
    align-items: center;
    flex-direction: column;
}

@media (max-width: 599px) {
    .yt-loop__container {
        width: 100%;
    }
}

@media(min-width: 1024px) {
    .yt-loop__container {
        width: 50%;
    }
}

.yt-frame {
    width: 100%;
    position: relative;
    padding-bottom: 56.25%; /* 16:9 */
}

.yt-frame iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.yt-loop__controls {
    margin: auto;

}

.yt-loop__controls > *+* {
    margin-top: 1em;
}


.time-input-boxes {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

}

.time-input-boxes > *+* {
    margin-left: 4em;
}

.time-input-boxes {
    height: 100%;
}

.additional-options {
    display: flex;
    justify-content: center;
}

.additional-options > *+* {
    margin-left: 2em;
}

.video-speed button {
    background-image: linear-gradient(to top, orange, #bb7b00);
    border-color: #bb7b00;
}

.control-section {
    margin-top: 2em;
}