.modal {
    display: none;
}
.modal__backdrop {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0,0,0,0.5);
    z-index: 120;
}

.modal__content {
    position: fixed;
    top: 20%;
    left: 40%;
    right: 40%;
    background-color: white;
    z-index: 140;
    border: 1px solid #0c82ff;
}

@media (max-width: 599px) {
   .modal__content {
        left: 5%;
        right: 5%;
   }
}

.modal__header {
    width: 100%;
    height: 24px;
    background-image: linear-gradient(to top, #32affb, #0077bf);
}


.modal__title {
    display: flex;
    align-items: center;
    color: white;
    height: 100%;
    margin-left: 1em;
    font-size: 12px;
}