.close {
    height: 20px;
    width: 20px;
    position: absolute;
    cursor: pointer;
    top: 0.2em;
    right: 0.2em;
    z-index: 130;
}

.close:before, .close:after {
    height: 100%;
    width: 1px;
    content: ' ';
    background-color: white;
    display: inline-block;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}

.close:before {
    transform: translateX(-50%) rotate(45deg);
}

.close:after {
    transform: translateX(-50%) rotate(-45deg);
}